.mainvisual {
    position: relative;
    padding: 2.0rem 0;
    background: #FBF8E7;
    .mainvisual-bg {
        position: relative;
        width: 100%;
        max-width: 120.0rem;
        margin-left: auto;
        margin-right: auto;
    }
}

.pageindex {
    position: relative;
    padding: 4.0rem 0 4.8rem;
    height: 20.0rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../img/pageindex.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .pageindex_wrapper {
        position: relative;
        flex: 0 0 100%;
        width: 100%;
    }
    .pageindex-title {
        position: relative;
        display: block;
        font-weight: 900;
        font-size: 3.6rem;
        letter-spacing: 0.2em;
        line-height: 4.8rem;
        text-align: center;
        color: $green_color;
        width: fit-content;
        white-space: nowrap;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
        @include media('sp') {
            font-size: 3.0rem;
            letter-spacing: 0.1em;
            line-height: 4.5rem;
        }
        span {
            position: relative;
            color: $green_color;
            z-index: 3;
        }
        strong {
            position: absolute;
            top: 0;
            left: 0;
            color: $green_color;
            -webkit-text-fill-color: $green_color;
            -webkit-text-stroke-width: 0.8rem;
            -webkit-text-stroke-color: #FFFFFF;
            z-index: 1;
            @include media('sp') {
                -webkit-text-stroke-width: 0.6rem;
            }
        }
    }
}

.breadcrumbs {
    position: relative;
    padding: 1.6rem 0;
    ol {
        display: block;
        list-style: none;
        margin: 0;
        font-family: $font_noto;
        font-size: 1.6rem;
        line-height: 2.4rem;
        font-weight: normal;
        color: #000000;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
        a {
            color: $green_color;
            text-decoration: underline;
        }
        li {
            position: relative;
            display: inline;
            &+li {
                &::before {
                    content: "\f105";
                    display: inline-block;
                    font-family: "Font Awesome 6 Free";
                    font-size: inherit;
                    font-weight: 900;
                    font-style: normal;
                    padding-left: 1.2rem;
                    padding-right: 1.2rem;
                    @include media('sp') {
                        padding-left: 1.0rem;
                        padding-right: 1.0rem;
                    }
                }
            }
        }
    }
}

.inner-toppage {
    position: relative;
    padding: 5.0rem 0 6.0rem;
    @include media('sp') {
        padding: 4.0rem 0 5.0rem;
    }
}

.contact-banner {
    position: relative;
    padding: 4.0rem 5.0rem 4.5rem;
    border: 0.5rem solid $green_color;
    background-image: url(../img/contact-bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include media('sp') {
        padding: 2.8rem 1.6rem 3.0rem;
        border-width: 0.2rem;
    }
    .inner {
        position: relative;
        max-width: 69.6rem;
        width: 100%;
        margin-left: auto;
        margin-right: 0;
        z-index: 1;
        .title {
            position: relative;
            font-weight: 900;
            font-size: 3.0rem;
            letter-spacing: 0.08em;
            line-height: 4.2rem;
            text-align: center;
            color: $green_color;
            margin-bottom: 2.4rem;
            @include media('sp') {
                font-size: 2.4rem;
                line-height: 3.6rem;
                text-shadow: -1px -1px 0px #FFFFFF, 0px -1px 0px #FFFFFF, 1px -1px 0px #FFFFFF, -1px 0px 0px #FFFFFF, 1px 0px 0px #FFFFFF, -1px 1px 0px #FFFFFF, 0px 1px 0px #FFFFFF, 1px 1px 0px #FFFFFF, -2px -2px 0px #FFFFFF, -1px -2px 0px #FFFFFF, 0px -2px 0px #FFFFFF, 1px -2px 0px #FFFFFF, 2px -2px 0px #FFFFFF, 2px -1px 0px #FFFFFF, 2px 0px 0px #FFFFFF, 2px 1px 0px #FFFFFF, 2px 2px 0px #FFFFFF, 1px 2px 0px #FFFFFF, 0px 2px 0px #FFFFFF, -1px 2px 0px #FFFFFF, -2px 2px 0px #FFFFFF, -2px 1px 0px #FFFFFF, -2px 0px 0px #FFFFFF, -2px -1px 0px #FFFFFF;
                margin-bottom: 2.0rem;
            }
        }
        .text {
            position: relative;
            font-weight: 500;
            font-size: 1.6rem;
            letter-spacing: 0.1em;
            line-height: 2.8rem;
            text-align: center;
            color: #000;
        }
        .actions {
            position: relative;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            margin-left: -1.0rem;
            margin-right: -1.0rem;
            margin-top: 4.0rem;
            @include media('sp') {
                margin-top: 3.0rem;
            }
            &>li {
                position: relative;
                padding: 0 1.0rem;
                @include media('sp') {
                    flex: 0 0 auto;
                    width: 100%;
                    &:not(:last-child) {
                        margin-bottom: 2.4rem;
                    }
                }
            }
            .phone-btn {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0;
                width: 32.6rem;
                max-width: 100%;
                img {
                    width: 100%;
                    height: auto;
                }
            }
            .mail-btn {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                background: #FF7100;
                width: 34.0rem;
                max-width: 100%;
                height: 8.6rem;
                text-decoration: none;
                outline: none;
                padding: 1.0rem 2.0rem;
                border: 2px solid #FF7100;
                border-radius: 1.2rem;
                transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                overflow: hidden;
                cursor: pointer;
                z-index: 1;
                @include media('sp') {
                    padding: 1.0rem 1.4rem;
                }
                .btn-icon {
                    position: relative;
                    flex: 0 0 auto;
                    width: 5.0rem;
                    z-index: 3;
                    path {
                        transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                    }
                }
                .btn-text {
                    position: relative;
                    flex: 0 0 auto;
                    width: calc(100% - 5.0rem);
                    font-family: $font_noto;
                    font-size: 2.0rem;
                    line-height: 3.0rem;
                    font-weight: 900;
                    letter-spacing: 0.05em;
                    color: #FFFFFF;
                    text-align: center;
                    white-space: nowrap;
                    text-decoration: none;
                    padding-left: 1.4rem;
                    z-index: 3;
                    @include media('sp') {
                        padding-left: 1.0rem;
                    }
                    small {
                        position: relative;
                        display: block;
                        font-size: 1.6rem;
                        font-weight: bold;
                        line-height: 2.4rem;
                        color: #FED517;
                        transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                        margin-bottom: 0.2rem;
                    }
                    strong {
                        position: relative;
                        display: block;
                        font-size: inherit;
                        line-height: inherit;
                        color: inherit;
                        transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                        @include media('sp') {
                            letter-spacing: 0;
                        }
                    }
                }
                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    background: #FFFFFF;
                    width: 100%;
                    height: 100%;
                    transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                    transform-origin: right top;
                    transform: scale(0, 1);
                    z-index: 1;
                }
                &:hover {
                    .btn-icon path {
                        fill: #FF7100;
                    }
                    .btn-text {
                        color: #FF7100;
                        small {
                            color: #FF7100;
                        }
                        strong {
                            color: #FF7100;
                        }
                    }
                    &::before {
                        transform-origin: left top;
                        transform: scale(1, 1);
                    }
                }
            }
        }
    }
    .effect {
        position: absolute;
        display: block;
        left: -3.0rem;
        bottom: 0;
        width: 39.3rem;
        z-index: 0;
        @include media('sp') {
            top: -3.0rem;
            left: auto;
            right: -1.5rem;
            bottom: auto;
            width: 10.0rem;
        }
    }
}

