.top-recommended {
    position: relative;
    padding: 10.0rem 0;
    background: #7EBF41;
    @include media('sp') {
        padding: 6.0rem 0 7.0rem;
    }
    .section-title {
        position: relative;
        .lead {
            position: relative;
            color: #FFFFFF;
            .left {
                position: absolute;
                top: 50%;
                left: -10.2rem;
                display: block;
                width: initial;
                max-width: 100%;
                transform: translateY(-50%);
                @include media('sp') {
                    width: 3.9rem;
                    left: -5.3rem;
                }
            }
            .right {
                position: absolute;
                top: 50%;
                right: -10.8rem;
                display: block;
                width: initial;
                max-width: 100%;
                transform: translateY(-50%);
                @include media('sp') {
                    width: 5.75rem;
                    right: -6.0rem;
                }
            }
        }
        .sub {
            color: #FFFFFF;
        }
    }
    .section-content {
        position: relative;
    }
    .recommended-swiper {
        position: relative;
        width: 100%;
        padding: 0 clamp(1.5rem, calc(50% - 51.0rem), 50%) 1.0rem;
        .swiper-slide {
            position: relative;
            width: 41.0rem;
            height: auto;
            @include media('sp') {
                width: 24.0rem;
            }
        }
        .recommended-item {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            border-radius: 2.0rem;
            box-shadow: 0.2rem 0.2rem 0.4rem rgba($color: #000000, $alpha: 0.1);
            overflow: hidden;
            @include media('sp') {
                border-radius: 1.2rem;
            }
            .thumb {
                position: relative;
                flex: 0 0 auto;
                width: 100%;
                aspect-ratio: 410 / 289;
                border-radius: 2.0rem 2.0rem 0 0;
                @include media('sp') {
                    border-radius: 1.2rem 1.2rem 0 0;
                }
            }
            .wrap {
                position: relative;
                flex: 1 auto;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 100%;
                padding: 3.2rem 3.2rem;
                @include media('sp') {
                    padding: 2.4rem 1.5rem;
                }
                .content {
                    position: relative;
                    flex: 1 auto;
                    width: 100%;
                    .desc {
                        position: relative;
                        font-weight: 400;
                        font-size: 1.6rem;
                        line-height: 2.8rem;
                        letter-spacing: 0.1em;
                        color: #000000;
                        margin-bottom: 0.6rem;
                    }
                    .name {
                        position: relative;
                        font-weight: 500;
                        font-size: 22px;
                        letter-spacing: 0.1em;
                        color: #000000;
                        @include media('sp') {
                            font-size: 1.8rem;
                        }
                    }
                }
                .action {
                    position: relative;
                    flex: 0 0 auto;
                    width: 100%;
                    margin-top: 3.2rem;
                    @include media('sp') {
                        margin-top: 2.0rem;
                    }
                    .link-btn {
                        position: relative;
                        background: #FFFFFF;
                        color: #3B8E65;
                        @include media('sp') {
                            padding-left: 2.4rem;
                            padding-right: 4.0rem;
                        }
                        &::before {
                            background: #3B8E65;
                        }
                        &:hover {
                            color: #FFFFFF;
                            svg path {
                                stroke: #FFFFFF;
                            }
                        }
                    }
                }

            }
        }
        .swiper-button-next, .swiper-button-prev {
            position: absolute;
            top: 50%;
            width: 7.0rem;
            height: 7.2rem;
            background: rgba($color: #1D6A1A, $alpha: 0.7);
            border-radius: 50%;
            color: #FFFFFF;
            margin: 0;
            transform: translateY(-50%);
            @include media('sp') {
                width: 3.6rem;
                height: 3.8rem;
            }
            &::after {
                font-size: 3.8rem;
                color: #FFFFFF;
                @include media('sp') {
                    font-size: 2.0rem;
                }
            }
        }
        .swiper-button-prev {
            left: clamp(1.5rem, calc(50% - 60.0rem), 40%);
            padding-right: 0.5rem;
            @include media('sp') {
                left: 1.5rem;
                padding-right: 0.2rem;
            }
        }
        .swiper-button-next {
            right: clamp(1.5rem, calc(50% - 60.0rem), 40%);
            padding-left: 0.5rem;
            @include media('sp') {
                right: 1.5rem;
                padding-left: 0.2rem;
            }
        }
    }
    .plan-action {
        position: relative;
        margin-top: 9.0rem;
        @include media('sp') {
            margin-top: 5.0rem;
        }
    }
}

