            .b {
                position: relative;
                font-weight: 700;
            }
            strong {
                display: inline-block;
                font-weight: 700;
                font-size: 1.8rem;
                margin-right: 1.0rem;
                @include media('sp') {
                    display: block;
                    width: 100%;
                    margin: 0;
