.calendar-wrapper {
    position: relative;
    .calendar-swiper {
        position: relative;
        padding: 0 4.5rem;
        @include media('sp') {
            padding: 0;
        }
        .swiper-slide {
            position: relative;
            height: auto;
            background: #FFFFFF;
            padding: 2.0rem 1.0rem 1.0rem;
            border: 1px solid #BBBBBB;
            @include media('sp') {
                padding: 1.8rem 0.8rem 0.8rem;
            }
        }
        .calendar {
            position: relative;
            width: 100%;
            border-collapse: collapse;
            font-weight: 400;
            font-size: 1.8rem;
            line-height: 3.0rem;
            color: #000000;
            white-space: nowrap;
            text-align: center;
            @include media('sp') {
                font-size: 1.6rem;
                line-height: 2.4rem;
            }
            th, td {
                position: relative;
                padding: 0.2rem 1.0rem 1.8rem;
                border: 2px solid #FFFFFF;
                @include media('sp') {
                    border-width: 1px;
                    padding: 0.3rem 0.8rem 1.6rem;
                }
                &:first-of-type {
                    color: #D93030;
                }
                &:last-of-type {
                    color: #1540BF;
                }
            }
            th {
                font-weight: 500;
            }
            .month {
                font-weight: 700;
                font-size: 2.0rem;
                color: #000000 !important;
            }
            .day {
                position: relative;
                &::before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    bottom: -1.0rem;
                    display: inline-block;
                    width: 1.2rem;
                    height: 1.2rem;
                    border: 1px solid #F15A24;
                    border-radius: 50%;
                    transform: translateX(-50%);
                    @include media('sp') {
                        width: 1.0rem;
                        height: 1.0rem;
                    }
                }
            }
            .red {
                color: #D93030;
            }
            .blue {
                color: #1540BF;
            }
            .old {
                color: #BBBBBB !important;
                .day::before {
                    display: none;
                }
            }
            tbody {
                td {
                    cursor: pointer;
                    &:hover,
                    &.active {
                        background: #FCF5B9;
                    }
                }
                .old, .new {
                    cursor: auto;
                    pointer-events: none;
                    &:hover,
                    &.active {
                        background: none;
                    }
                }
            }
        }
    }
    
    .swiper-button-next, .swiper-button-prev {
        &::after {
            color: #000000;
            @include media('sp') {
                font-size: 4.0rem;
            }
        }
    }
    .swiper-button-prev {
        left: 0;
        @include media('sp') {
            left: -2.7rem;
        }
    }
    .swiper-button-next {
        right: 0;
        @include media('sp') {
            right: -2.7rem;
        }
    }
}


.apply-lead-section {
