.contact-form {
    position: relative;
    display: block;
    font-weight: 700;
    font-size: 1.8rem;
    letter-spacing: 0.1em;
    color: #272727;
    max-width: 104.0rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    @include media('sp') {
        font-size: 1.6rem;
        line-height: 2.8rem;
    }
    .hide {
        display: none;
    }
    .form-group {
        position: relative;
        display: block;
        & + .form-group {
            margin-top: 4.0rem;
            @include media('sp') {
                margin-top: 2.4rem;
            }
        }
        .label {
            position: relative;
            display: block;
            text-align: left;
            margin-bottom: 1.6rem;
            @include media('sp') {
                margin-bottom: 1.0rem;
            }
            .required {
                position: relative;
                display: inline-block;
                padding: 0.2rem 1.0rem 0.3rem 1.2rem;
                background: #C60D0D;
                font-size: 1.6rem;
                line-height: 2.4rem;
                font-weight: 500;
                letter-spacing: 0.2em;
                border-radius: 0.4rem;
                color: #FFFFFF;
                text-align: center;
                margin-left: 1.6rem;
                @include media('sp') {
                    font-size: 1.4rem;
                    line-height: 2.1rem;
                }
            }
        }
        input[type="text"],
        input[type="email"],
        input[type="tel"],
        input[type="number"],
        input[type="password"],
        select,
        textarea {
            border: 2px solid #3B8E65;
        }
        .input {
            position: relative;
            input,
            select,
            textarea {
                vertical-align: middle;
            }
            span {
                vertical-align: bottom;
            }
        }
        .sm {
            width: 27.0rem;
            max-width: 100%;
        }
        .s {
            width: 12.0rem;
            max-width: 100%;
            @include media('sp') {
                width: 100%;
            }
        }
        .datetime-inputs {
            position: relative;
            list-style: none;
            display: flex;
            margin-left: -0.6rem;
            margin-right: -0.6rem;
            @include media('sp') {
                margin-left: -0.3rem;
                margin-right: -0.3rem;
            }
            &>li {
                position: relative;
                flex: 0 0 auto;
                display: flex;
                align-items: center;
                padding: 0 0.6rem;
                @include media('sp') {
                    padding: 0 0.3rem;
                    width: 33.3333%;
                }
                .unit {
                    position: relative;
                    display: inline-block;
                    padding-left: 0.6rem;
                    @include media('sp') {
                        padding-left: 0.3rem;
                    }
                }
                .month {
                    position: relative;
                    width: 12.0rem;
                    @include media('sp') {
                        width: calc(100% - 2.1rem);
                    }
                }
                .day,
                .time {
                    position: relative;
                    width: 10.0rem;
                    @include media('sp') {
                        width: calc(100% - 2.1rem);
                    }
                }
            }
        }
    }
    .form-action {
        position: relative;
        margin-top: 6.0rem;
        @include media('sp') {
            margin-top: 4.0rem;
        }
        .link-btn {
            font-weight: 700;
            font-size: 2.4rem;
            line-height: 3.6rem;
            padding: 2.0rem 2.0rem;
            width: 40.0rem;
            max-width: 100%;
            border-radius: 3.8rem;
            margin-left: auto;
            margin-right: auto;
            @include media('sp') {
                font-size: 1.8rem;
                width: 24.0rem;
                line-height: 3.0rem;
                padding: 1.5rem 2.0rem;
                border-radius: 3.0rem;
            }
            &::after {
                border-radius: 3.8rem;
                @include media('sp') {
                    border-radius: 3.0rem;
                }
            }
        }
    }
}



