.mobile-nav-toggle {
    position: fixed;
    right: 0;
    top: 0;
    width: 6rem;
    height: 6rem;
    padding: 1.8rem 1.5rem;
    background: $green_color;
    display: none;
    border: none;
    outline: none;
    cursor: pointer;
    z-index: 999;
    margin: 0;
    transition: all 0.4s ease-in-out;
    @include media("sp") {
        display: inline-block;
    }
    .toggle-icon {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        span {
            display: inline-block;
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            background: #ffffff;
            transition: all 0.4s ease-in-out;
            transform-origin: center center;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: calc(50% - 1px);
            }
            &:nth-child(3) {
                bottom: 0;
            }
        }
    }
    &.toggle-active {
        .toggle-icon {
            span {
                &:nth-child(1) {
                    transform: translateY(1rem) rotate(-40deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                    visibility: hidden;
                }
                &:nth-child(3) {
                    transform: translateY(-1.1rem) rotate(40deg);
                }
            }
        }
    }
}

// Mobile Nav
#mobile-nav {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 998;
    right: -100%;
    width: 80%;
    max-width: 600px;
    overflow-y: auto;
    transition: all 0.4s ease-in-out;
    background: $green_color;
    .mobile-nav-container {
        position: relative;
        padding: 13rem 3rem 3rem;
        @include media("sp") {
            padding: 6rem 1.5rem 1.5rem;
        }
    }
    .mobile-nav-menu {
        position: relative;
        list-style: none;
        & > li {
            position: relative;
            display: block;
            & > a {
                position: relative;
                display: flex;
                align-items: center;
                width: 100%;
                font-family: $font_noto;
                font-weight: 400;
                font-size: 1.6rem;
                line-height: 2.4rem;
                letter-spacing: 0.1em;
                font-weight: 500;
                text-align: left;
                color: #ffffff;
                padding: 1.8rem 0;
                text-decoration: none;
                outline: none;
                border-bottom: 1px solid #FFFFFF;
                img {
                    display: block;
                    flex: 0 0 auto;
                    width: 3.7rem;
                }
                span {
                    color: inherit;
                }
            }
        }
        .drop-down {
            & > a {
                &::after {
                    content: "";
                    position: absolute;
                    top: calc(50% - 3px);
                    right: 0;
                    display: inline-block;
                    width: 10px;
                    height: 6px;
                    background-image: url(../img/common/icon-down.png);
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    z-index: 2;
                }
            }
            ul {
                display: none;
                overflow: hidden;
            }
            li {
                padding-left: 16px;
                a {
                    position: relative;
                    display: block;
                    width: 100%;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 24px;
                    text-align: left;
                    color: #000000;
                    padding: 16px 0;
                    text-decoration: none;
                    outline: none;
                    border-bottom: 1px solid #000000;
                }
            }
            &.active {
                & > a::after {
                    transform: rotate(180deg);
                    transform-origin: center center;
                }
            }
        }
    }
}

.mobile-nav-overly {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 997;
    background: rgba($color: #000000, $alpha: 0.5);
    display: none;
    overflow: hidden;
    transition: all 0.2s ease-in-out;
}

/* Mobile Nav body classes */
body.mobile-nav-active {
    @include media("sp") {
        overflow: hidden;
    }
}
body.mobile-nav-active #mobile-nav {
    right: 0;
}
