/* input */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #ffffff;
  border: 2px solid #bbbbbb;
  box-sizing: border-box;
  padding: 1.2rem 1.6rem;
  width: 100%;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-align: left;
  color: #1b1b1f;
  border-radius: 0.6rem;
}

@media only screen and (max-width: 768px) {
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  input[type="number"],
  input[type="password"],
  select,
  textarea {
    font-size: 1.6rem;
    line-height: 2.4rem;
    padding: 1.2rem 1.6rem;
  }
}

input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="tel"]::placeholder,
input[type="number"]::placeholder,
input[type="password"]::placeholder,
select::placeholder,
textarea::placeholder {
  color: #BBBBBB;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 5.0rem;
}

@media only screen and (max-width: 768px) {
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  input[type="number"],
  input[type="password"],
  select {
    height: 5.0rem;
  }
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #3b8e65;
  outline: 0.3rem solid #7ebf41;
}

select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url(../img/select-down.png);
  background-position: calc(100% - 1.6rem) 50%;
  background-size: 1.6rem 0.9rem;
  background-repeat: no-repeat;
  z-index: 0;
}

@media only screen and (max-width: 768px) {
