.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  outline: 0;
  opacity: 0;
  transition: opacity .1s linear;
  padding: 1.5rem;
  overflow-x: hidden;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.5);
}

.modal .modal-dialog {
  position: relative;
  max-width: 70.0rem;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
  transform: translate(0, -15%);
  margin: auto;
}

.modal .modal-content {
  position: relative;
  display: block;
  width: 100%;
  background: #FFFFFF;
  border-radius: 0.6rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  pointer-events: auto;
}

.modal .close {
  position: absolute;
  top: -1.0rem;
  right: -1.0rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2rem 0.6rem;
  width: 3.0rem;
  height: 3.0rem;
  background-color: #FED517;
  border-radius: 50%;
  font-size: 1.6rem;
  line-height: 1;
  z-index: 3;
}

.modal .modal-body {
  position: relative;
  padding: 2.0rem;
}

.modal.show {
  opacity: 1;
}

.modal.show .modal-dialog {
  transform: translate(0, 0);
}

.modal-open {
  overflow: hidden;
}

