#footer {
    position: relative;
    background: #7EBF41;
    padding: 6.0rem 0 4.0rem;
    margin: 0;
    z-index: 0;
    @include media('sp') {
        padding: 6.0rem 0 4.0rem;
    }
    .footer-logo {
        position: relative;
        margin-bottom: 5.0rem;
        @include media('sp'){
            margin-bottom: 4.0rem;
        }
        a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 29.0rem;
            max-width: 100%;
            margin-left: auto;
            margin-right: auto;
            z-index: 0;
            @include media('sp') {
                width: 23.02rem;
            }
            img {
                display: block;
                width: 100%;
            }
        }
    }
    .footer-info {
        position: relative;
        margin-bottom: 3.0rem;
        .text {
            position: relative;
            font-size: 1.6rem;
            line-height: 2.8rem;
            letter-spacing: 0.1em;
            color: #FFFFFF;
            text-align: center;
            a {
                color: inherit;
                text-decoration: underline;
            }
        }
    }
    .footer-nav {
        position: relative;
        .nav-menu {
            position: relative;
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 1;
            @include media('sp') {
                flex-wrap: wrap;
            }
            & > li {
                position: relative;
                padding: 0;
                @include media('pc') {
                    &:not(:last-child) {
                        border-right: 1px solid #CFCFCF;
                    }
                }
                @include media('sp') {
                    flex: 0 0 auto;
                    width: 100%;
                }
                .menu-link {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: fit-content;
                    font-family: $font_mplus;
                    font-size: 1.6rem;
                    font-weight: 700;
                    line-height: 3.7rem;
                    letter-spacing: 0.1em;
                    color: #4E3B13;
                    padding: 0.6rem 3.2rem;
                    margin: 0;
                    white-space: nowrap;
                    text-decoration: none;
                    cursor: pointer;
                    z-index: 0;
                    transition: all 0.2s ease-in-out;
                    @include media('sp') {
                        width: 26.0rem;
                        padding: 0.6rem 2.0rem;
                        justify-content: flex-start;
                        margin-left: auto;
                        margin-right: auto;
                    }
                    img {
                        display: block;
                        flex: 0 0 auto;
                        width: 3.7rem;
                    }
                    span {
                        color: inherit;
                    }
                    &:hover,
                    &.active {
                        color: #4E3B13;
                    }
                }
            }
        }
        .subnav-menu {
            position: relative;
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 1;
            @include media('pc') {
                margin-top: 2.0rem;
            }
            @include media('sp') {
                flex-wrap: wrap;
            }
            & > li {
                position: relative;
                padding: 0;
                @include media('sp') {
                    flex: 0 0 auto;
                    width: 100%;
                }
                .menu-link {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: fit-content;
                    font-family: $font_mplus;
                    font-size: 1.6rem;
                    font-weight: 700;
                    line-height: 3.7rem;
                    letter-spacing: 0.1em;
                    padding: 0.6rem 3.2rem;
                    color: #4E3B13;
                    margin: 0;
                    white-space: nowrap;
                    text-decoration: none;
                    cursor: pointer;
                    z-index: 0;
                    transition: all 0.2s ease-in-out;
                    @include media('sp') {
                        width: 26.0rem;
                        padding: 0.6rem 2.0rem;
                        justify-content: flex-start;
                        margin-left: auto;
                        margin-right: auto;
                    }
                    img {
                        display: block;
                        flex: 0 0 auto;
                        width: 3.7rem;
                    }
                    span {
                        color: inherit;
                        @include media('sp') {
                            display: block;
                            padding-left: 3.7rem;
                        }
                    }
                    &:hover,
                    &.active {
                        color: #4E3B13;
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .copyright {
        position: relative;
        font-size: 1.4rem;
        line-height: 2.4rem;
        font-weight: 400;
        color: #FFFFFF;
        text-align: center;
        margin-top: 3.0rem;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
    }
}



.back_to_top {
    position: fixed;
    bottom: 30px;
    right: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    z-index: 996;
    @include media('sp') {
        width: 45px;
        right: 16px;
        bottom: 16px;
    }
    img {
        width: 100%;
        @include media('pc') {
            transition: all 0.4s;
        }
    }
    @include media('pc') {
        &:hover {
            text-decoration: none;
            img {
                filter: drop-shadow(0 0 12px #fff);
                -webkit-filter: drop-shadow(0 0 12px #fff);
            }
        }
    }
}

.fixed-side-btn {
    position: fixed;
    top: 30.0rem;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 5.0rem;
    height: 20.0rem;
    padding: 1.6rem 1.0rem;
    background: $red_color;
    border-radius: 0.6rem 0 0 0.6rem;
    font-family: $font_noto;
    font-weight: 500;
    font-size: 2.0rem;
    letter-spacing: 0;
    line-height: 2.4rem;
    text-align: center;
    color: #FFFFFF;
    border: none;
    text-decoration: none;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    z-index: 996;
    @include media('sp') {
        top: 15.0rem;
        width: 4.0rem;
        height: 17.0rem;
        font-size: 1.6rem;
        line-height: 2.0rem;
    }
    span {
        color: inherit;
        text-decoration: none;
    }
    &:hover {
        opacity: 0.7;
    }
}

.footer-fixed {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    cursor: pointer;
    z-index: 996;
    &>li {
        position: relative;
        flex: 0 0 50%;
        width: 50%;
    }
    .phone-btn {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        width: 100%;
        max-width: 100%;
        img {
            width: 100%;
            height: auto;
        }
    }
    .mail-btn {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #FF7100;
        width: 100%;
        max-width: 100%;
        height: 6.0rem;
        text-decoration: none;
        outline: none;
        padding: 1.0rem 1.2rem;
        border: 2px solid #FF7100;
        transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        overflow: hidden;
        cursor: pointer;
        z-index: 1;
        .btn-icon {
            position: relative;
            flex: 0 0 auto;
            width: 3.0rem;
            height: auto;
            z-index: 3;
            path {
                transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
            }
        }
        .btn-text {
            position: relative;
            flex: 0 0 auto;
            width: calc(100% - 3.0rem);
            font-family: $font_noto;
            font-size: 1.6rem;
            line-height: 2.4rem;
            font-weight: 900;
            letter-spacing: 0.04em;
            color: #FFFFFF;
            text-align: center;
            white-space: nowrap;
            text-decoration: none;
            padding-left: 1.0rem;
            z-index: 3;
            small {
                position: relative;
                display: block;
                font-size: 1.2rem;
                font-weight: bold;
                line-height: 1.8rem;
                color: #FED517;
                transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
            }
            strong {
                position: relative;
                display: block;
                font-size: inherit;
                line-height: inherit;
                color: inherit;
                transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
                @include media('sp') {
                    letter-spacing: 0;
                }
            }
        }
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            background: #FFFFFF;
            width: 100%;
            height: 100%;
            transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
            transform-origin: right top;
            transform: scale(0, 1);
            z-index: 1;
        }
        &:hover {
            .btn-icon path {
                fill: #FF7100;
            }
            .btn-text {
                color: #FF7100;
                small {
                    color: #FF7100;
                }
                strong {
                    color: #FF7100;
                }
            }
            &::before {
                transform-origin: left top;
                transform: scale(1, 1);
            }
        }
    }
}