#header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #FCFCFC;
    padding: 3.0rem 1.5rem;
    box-shadow: 0 0.2rem 0.2rem rgba($color: #000000, $alpha: 0.16);
    transition: all 0.2s ease-in-out;
    z-index: 996;
    @include media('sp') {
        position: sticky;
        height: 6.0rem;
        padding: 0.9rem 1.2rem 1.1rem;
    }
    .container {
        position: relative;
    }
    .header-logo {
        position: relative;
        width: 30.0rem;
        padding: 0;
        margin-left: auto;
        margin-right: auto;
        transition: all 0.2s ease-in-out;
        margin-bottom: 2.8rem;
        @include media('sp') {
            margin-left: 0;
            margin-bottom: 0;
            width: 17.65rem;
        }
        a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            img {
                display: block;
                width: 100%;
            }
        }
    }
    .header-nav {
        position: relative;
        width: 100%;
        padding: 0;
        transition: all 0.2s ease-in-out;
        @include media('tb') {
            display: none;
        }
        .nav-menu {
            position: relative;
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 1;
            & > li {
                position: relative;
                padding: 0;
                &:not(:last-child) {
                    border-right: 1px solid #CFCFCF;
                }
                .menu-link {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: fit-content;
                    font-family: $font_mplus;
                    font-size: 1.6rem;
                    font-weight: 700;
                    line-height: 3.7rem;
                    letter-spacing: 0.1em;
                    color: #4E3B13;
                    padding: 0.6rem 3.2rem;
                    margin: 0;
                    white-space: nowrap;
                    text-decoration: none;
                    cursor: pointer;
                    z-index: 0;
                    transition: all 0.2s ease-in-out;
                    img {
                        display: block;
                        flex: 0 0 auto;
                        width: 3.7rem;
                    }
                    span {
                        color: inherit;
                    }
                    &:hover,
                    &.active {
                        color: #7EBF41;
                    }
                }
            }
        }
    }
    @include media('pc') {
        &.header-scrolled {
            .header-logo {
                opacity: 0;
                visibility: hidden;
                width: 0rem;
                height: 0rem;
                margin-bottom: 0;
            }
        }
    }
}