// section
.common-section {
    position: relative;
    padding: 10.0rem 0 10.0rem;
    @include media('sp') {
        padding: 6.0rem 0 7.0rem;
    }
}

// text set
.m-text {
    position: relative;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.8rem;
    @include media('sp') {
        letter-spacing: 0.04em;
    }
}

.underline-bg {
    display: inline;
    background: linear-gradient(transparent 68%, #FBB03B 0%);
}

.section-title {
    position: relative;
    padding-bottom: 4.0rem;
    margin-bottom: 5.0rem;
    @include media('sp') {
        padding-bottom: 2.5rem;
        margin-bottom: 4.0rem;
    }
    .lead {
        position: relative;
        display: block;
        width: fit-content;
        max-width: 100%;
        font-family: $font_mplus;
        font-weight: 700;
        font-size: 3.2rem;
        line-height: 4.2rem;
        letter-spacing: 0.15em;
        color: #4E3B13;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        @include media('sp') {
            font-size: 2.4rem;
            line-height: 3.2rem;
            letter-spacing: 0.1em;
        }
    }
    .sup {
        position: relative;
        font-weight: 700;
        font-size: 2.2rem;
        text-align: center;
        letter-spacing: 0.2em;
        color: #4E3B13;
        text-align: center;
        margin-bottom: 0.6rem;
        @include media('sp') {
            font-size: 1.8rem;
            letter-spacing: 0.1em;
            margin-bottom: 0.2rem;
        }
    }
    .sub {
        position: relative;
        font-weight: 400;
        font-size: 1.6rem;
        line-height: 2.8rem;
        letter-spacing: 0.1em;
        color: #4E3B13;
        text-align: center;
        margin-top: 0.6rem;
        @include media('sp') {
            margin-top: 0.2rem;
        }
    }
    &::before {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        display: inline-block;
        width: 4.0rem;
        height: 0.5rem;
        background-image: url(../img/title-border.png);
        background-position: center;background-size: cover;
        transform: translateX(-50%);
    }
}

.section-desc {
    position: relative;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 3.0rem;
    text-align: center;
    letter-spacing: 0.1em;
    color: #4E3B13;
    margin-bottom: 6.0rem;
    @include media('sp') {
        font-size: 1.6rem;
        line-height: 2.8rem;
        text-align: left;
        margin-bottom: 5.0rem;
    }
}

.sub-title {
    position: relative;
    margin-bottom: 6.0rem;
    @include media('sp') {
        margin-bottom: 4.0rem;
    }
    .lead {
        position: relative;
        padding-left: 8.0rem;
        font-weight: 700;
        font-size: 3.0rem;
        color: #4E3B13;
        @include media('sp') {
            font-size: 2.4rem;
            padding-left: 6.0rem;
            padding: 0;
            text-align: center;
        }
        .left {
            position: absolute;
            top: -2.4rem;
            left: -1.0rem;
            width: fit-content;
            @include media('sp') {
                top: -1.6rem;
                left: -0.6rem;
                width: 7.2rem;
                display: none;
            }
        }
        span {
            color: inherit;
        }
    }
    &.type1 {
        margin-bottom: 5.0rem;
        @include media('sp') {
            margin-bottom: 3.0rem;
        }
        .lead {
            padding-left: 6.8rem;
            @include media('sp') {
                padding-left: 5.0rem;
                text-align: left;
            }
            .left {
                top: 0;
                left: 0;
                @include media('sp') {
                    top: 0.4rem;
                    left: 0;
                    width: 3.6rem;
                    display: block;
                }
            }
        }
    }
}

.section-description {
    position: relative;
    font-size: 1.8rem;
    line-height: 3.0rem;
    font-weight: bold;
    letter-spacing: 0.1em;
    text-align: center;
    margin-bottom: 5rem;
    @include media('sp') {
        font-size: 1.6rem;
        line-height: 2.8rem;
        letter-spacing: 0.08em;
        text-align: left;
    }
}

.common-title {
    position: relative;
    font-weight: 900;
    font-size: 3.0rem;
    letter-spacing: 0.1em;
    line-height: 4.8rem;
    text-align: center;
    color: #000000;
    margin-bottom: 5.0rem;
    @include media('sp') {
        font-size: 2.4rem;
        line-height: 3.6rem;
        letter-spacing: 0.05em;
        margin-bottom: 4.0rem;
    }
    strong {
        font-size: 5.0rem;
        color: $red_color;
        @include media('sp') {
            font-size: 3.6rem;
            line-height: 4.0rem;
        }
    }
    span {
        font-size: 3.6rem;
        @include media('sp') {
            font-size: 3.0rem;
        }
    }
}


/* btn */

.btn-wrap {
    position: relative;
}

.btn {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 20px;
    max-width: 240px;
    width: 100%;
    height: 50px;
    background: #F2F2F2;
    border: 1px solid #858585;
    font-family: $font_noto;
    font-size: 14px;
    line-height: 24px;
    font-weight: 300;
    color: #1A1A1A;
    text-align: center;
    text-decoration: none;
    outline: none;
    transition: all 0.4s;
    cursor: pointer;
    @include media('sp') {
        height: 45px;
        max-width: 200px;
    }
    span {
        position: relative;
        color: inherit;
        text-decoration: none;
        z-index: 3;
    }
    &:hover {
        background: #A10707;
        border-color: #A10707;
        color: #FFFFFF;
    }
}

.viewmore_link_wrap {
    position: relative;
    .viewmore_link {
        position: relative;
        display: inline-block;
        font-family: $font_ubuntu;
        font-weight: bold;
        font-style: italic;
        font-size: 16px;
        line-height: 26px;
        color: $orange_color;
        text-decoration: none;
        &::before {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: $orange_color;
        }
        &::after {
            content: ">";
            display: inline-block;
            line-height: 1;
            margin-left: 6px;
            vertical-align: text-bottom;
        }
        &:hover {
            &::before {
                width: 100%;
                animation: link-lineloop 0.6s ease-out;
                -webkit-animation: link-lineloop 0.6s ease-out;
            }
        }
    }
}

.view-more {
    position: relative;
}

.link-btn {
    position: relative;
    overflow: hidden;
    display: block;
    width: fit-content;
    max-width: 100%;
    padding: 1.3rem 7.0rem 1.3rem 4.0rem;
    background-color: #3B8E65;
    font-family: $font_mplus;
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.2em;
    color: #FFFFFF;
    text-align: center;
    text-decoration: none;
    outline: none;
    transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
    border-radius: 2.5rem;
    cursor: pointer;
    z-index: 0;
    @include media('sp') {
        padding: 1.3rem 6.0rem 1.3rem 3.2rem;
        font-size: 1.6rem;
        line-height: 2.4rem;
        letter-spacing: 0.1em;
        border-radius: 2.5rem;
    }
    span {
        position: relative;
        color: inherit;
        text-decoration: none;
        z-index: 3;
    }
    svg {
        position: absolute;
        top: 50%;
        right: 4.0rem;
        display: block;
        width: initial;
        height: auto;
        transform: translateY(-50%);
        z-index: 3;
        @include media('sp') {
            right: 3.2rem;
        }
        path {
            transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        }
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
        border-radius: 2.5rem;
        border: 2px solid #3B8E65;
        z-index: 2;
        @include media('sp') {
            border-width: 2px;
        }
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        background: #FFFFFF;
        width: 100%;
        height: 100%;
        transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        transform-origin: right top;
        transform: scale(0, 1);
        z-index: 1;
    }
    &:hover {
        color: #3B8E65;
        svg path {
            stroke: #3B8E65;
        }
        &::before {
            transform-origin: left top;
            transform: scale(1, 1);
        }
    }
}

.outline-btn {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    max-width: 100%;
    padding: 1.5rem 4.0rem;
    background-color: #FFFFFF;
    font-family: $font_mplus;
    font-weight: 700;
    font-size: 1.8rem;
    line-height: 3.0rem;
    letter-spacing: 0.1em;
    color: #3B8E65;
    white-space: nowrap;
    text-decoration: none;
    outline: none;
    transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
    border-radius: 3.0rem;
    cursor: pointer;
    z-index: 0;
    @include media('sp') {
        padding: 1.6rem 3.0rem;
        font-size: 1.6rem;
        line-height: 2.4rem;
        letter-spacing: 0.05em;
        border-radius: 2.8rem;
        margin-left: auto;
        margin-right: auto;
    }
    span {
        position: relative;
        color: inherit;
        text-decoration: none;
        z-index: 3;
    }
    svg {
        position: relative;
        display: inline-block;
        width: 2.0rem;
        vertical-align: middle;
        margin-right: 1.0rem;
        z-index: 3;
        path {
            transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        }
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
        border-radius: 3.0rem;
        border: 2px solid #3B8E65;
        z-index: 2;
        @include media('sp') {
            border-width: 2px;
            border-radius: 2.8rem;
        }
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        background: #3B8E65;
        width: 100%;
        height: 100%;
        transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        transform-origin: right top;
        transform: scale(0, 1);
        z-index: 1;
    }
    &:hover {
        color: #FFFFFF;
        svg path {
            fill: #FFFFFF;
        }
        &::before {
            transform-origin: left top;
            transform: scale(1, 1);
        }
    }
}

.common-btn {
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 3.6rem;
    padding: 2.0rem 2.0rem;
    width: 40.0rem;
    max-width: 100%;
    border-radius: 3.8rem;
    margin-left: auto;
    margin-right: auto;
    @include media('sp') {
        font-size: 1.8rem;
        width: 24.0rem;
        line-height: 3.0rem;
        padding: 1.5rem 2.0rem;
        border-radius: 3.0rem;
    }
    &::after {
        border-radius: 3.8rem;
        @include media('sp') {
            border-radius: 3.0rem;
        }
    }
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    display: none;
    outline: 0;
    opacity: 0;
    transition: opacity .1s linear;
    padding: 1.5rem;
    overflow-x: hidden;
    overflow-y: auto;
    background: rgba($color: #000000, $alpha: 0.5);
    .modal-dialog {
        position: relative;
        max-width: 70.0rem;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        pointer-events: none;
        transition: transform .3s ease-out, -webkit-transform .3s ease-out;
        transform: translate(0, -15%);
        margin: auto;
    }
    .modal-content {
        position: relative;
        display: block;
        width: 100%;
        background: #FFFFFF;
        border-radius: 0.6rem;
        border: 1px solid rgba($color: #000000, $alpha: 0.2);
        pointer-events: auto;
    }
    .close {
        position: absolute;
        top: -1.0rem;
        right: -1.0rem;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0.2rem 0.6rem;
        width: 3.0rem;
        height: 3.0rem;
        background-color: $yellow_color;
        border-radius: 50%;
        font-size: 1.6rem;
        line-height: 1;
        z-index: 3;
    }
    .modal-body {
        position: relative;
        padding: 2.0rem;
    }
    &.show {
        opacity: 1;
        .modal-dialog {
            transform: translate(0, 0);
        }
    }
}

.modal-open {
    overflow: hidden;
}

.tabs {
    position: relative;
    max-width: 108.0rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    .tabs-list {
        position: relative;
        display: grid;
        grid-template-columns: repeat(2, minmax(0rem, 1fr));
        grid-column-gap: 0.3rem;
        @include media('sp') {
            grid-column-gap: 0.2rem;
        }
        & > li {
            position: relative;
        }
        .tabs-link {
            position: relative;
            display: block;
            width: 100%;
            height: 100%;
            background: #3B8E65;
            border-radius: 1.0rem 1.0rem 0 0;
            font-weight: 700;
            font-size: 1.8rem;
            line-height: 3.0rem;
            color: #FFFFFF;
            text-align: center;
            padding: 1.7rem 3.2rem;
            transition: all 0.3s ease-in-out;
            @include media('sp') {
                font-size: 1.6rem;
                line-height: 2.4rem;
                letter-spacing: 0;
                text-align: center;
                padding: 1.2rem 1.2rem;
                border-radius: 1.0rem 1.0rem 0 0;
            }
            &:hover,
            &.active {
                background: #FFFFFF;
                color: #3B8E65;
            }
        }
    }
    .tab {
        position: relative;
        display: none;
        .tab-content {
            position: relative;
            padding: 4.8rem 4.8rem;
            background: #FFFFFF;
            @include media('sp') {
                padding: 2.4rem 1.6rem;
            }
        }
        .area-selects {
            position: relative;
            display: grid;
            grid-template-columns: repeat(3, minmax(0rem, 1fr));
            grid-column-gap: 8.0rem;
            grid-row-gap: 3.2rem;
            max-width: 88.0rem;
            width: 100%;
            margin-left: auto;
            margin-right: auto;
            @include media('sp') {
                grid-template-columns: 100%;
                grid-column-gap: 2.0rem;
                grid-row-gap: 2.0rem;
            }
            & > li {
                position: relative;
            }
        }
        .google-map {
            position: relative;
            display: block;
            img,
            iframe {
                position: relative;
                display: block;
                width: 100%;
                vertical-align: middle;
                z-index: 0;
            }
            .search-box {
                position: absolute;
                top: 2.4rem;
                left: 2.4rem;
                width: 48.0rem;
                max-width: 100%;
                z-index: 3;
                @include media('sp') {
                    top: 1.5rem;
                    left: 1.5rem;
                    width: 27.0rem;
                }
                input {
                    position: relative;
                    display: block;
                    width: 100%;
                    border-color: #3B8E65;
                    z-index: 0;
                }
                button {
                    position: absolute;
                    top: 50%;
                    right: 1.2rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: transparent;
                    width: 4.2rem;
                    padding: 0;
                    margin: 0;
                    outline: none;
                    text-decoration: none;
                    transform: translateY(-50%);
                    z-index: 2;
                    @include media('sp') {
                        width: 3.2rem;
                    }
                    img {
                        display: block;
                        width: 100%;
                    }
                    &:focus {
                        outline: none;
                    }
                }
            }
        }
        .detail-form {
            position: relative;
            .form-lead {
                position: relative;
                font-weight: 700;
                font-size: 2.4rem;
                color: #4E3B13;
                margin-bottom: 6.0rem;
                @include media('sp') {
                    font-size: 1.6rem;
                    line-height: 2.8rem;
                    letter-spacing: -0.05em;
                    margin-bottom: 2.4rem;
                }
            }
            .form-group {
                position: relative;
                .label {
                    position: relative;
                    font-weight: 700;
                    font-size: 1.8rem;
                    color: #272727;
                    margin-bottom: 1.0rem;
                    @include media('sp') {
                        font-size: 1.6rem;
                        line-height: 2.8rem;
                        margin-bottom: 0.6rem;
                    }
                }
            }
            .keyword-input {
                position: relative;
                display: block;
                width: 100%;
                @include media('pc') {
                    display: flex;
                }
                input {
                    position: relative;
                    display: block;
                    border-color: #3B8E65;
                    z-index: 0;
                    @include media('pc') {
                        flex: 1 auto;
                        width: calc(100% - 15.2rem);
                        border-radius: 0.6rem 0 0 0.6rem;
                        border-right: none;
                    }
                    @include media('sp') {
                        width: 100%;
                        border-radius: 0.6rem;
                        margin-bottom: 2.0rem;
                    }
                    &:focus {
                        outline: none;
                    }
                }
                button {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: #3B8E65;
                    font-family: $font_mplus;
                    font-weight: 700;
                    font-size: 1.6rem;
                    line-height: 2.4rem;
                    color: #FFFFFF;
                    padding: 1.2rem 1.6rem 1.0rem;
                    height: 5.0rem;
                    margin: 0;
                    outline: none;
                    text-decoration: none;
                    z-index: 2;
                    @include media('pc') {
                        flex: 0 0 auto;
                        width: 15.2rem;
                        border-radius: 0 0.6rem 0.6rem 0;
                    }
                    @include media('sp') {
                        width: 18.0rem;
                        font-size: 1.6rem;
                        line-height: 2.4rem;
                        border-radius: 0.6rem;
                        margin-left: auto;
                        margin-right: auto;
                    }
                    img {
                        display: block;
                        width: 100%;
                    }
                    &:focus {
                        outline: none;
                    }
                }
            }
            .keyword-tags {
                position: relative;
                display: block;
                letter-spacing: 0;
                line-height: 1;
                margin-top: 0.8rem;
                margin-right: -1.2rem;
                @include media('sp') {
                    margin-top: 1.4rem;
                    margin-right: -1.0rem;
                }
                .keyword-tag {
                    position: relative;
                    display: inline-block;
                    padding: 0.8rem 1.6rem 0.8rem 2.4rem;
                    border: 1px solid #3B8E65;
                    border-radius: 2.0rem;
                    font-weight: 400;
                    font-size: 1.3rem;
                    line-height: 2.2rem;
                    letter-spacing: 0.1em;
                    white-space: nowrap;
                    color: #3B8E65;
                    text-align: center;
                    margin-right: 1.2rem;
                    margin-top: 1.0rem;
                    @include media('sp') {
                        padding: 0.6rem 1.0rem 0.6rem 1.6rem;
                        margin-right: 1.0rem;
                    }
                    input {
                        display: none;
                    }
                    span {
                        color: inherit;
                    }
                    i {
                        position: relative;
                        display: inline-block;
                        cursor: pointer;
                        margin-left: 0.5rem;
                    }
                }
            }
            .checkbox-list {
                position: relative;
                display: block;
                @include media('pc') {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    margin-left: -1.0rem;
                    margin-right: -1.0rem;
                }
                & > li {
                    position: relative;
                    @include media('pc') {
                        padding: 0 1.0rem;
                    }
                    @include media('sp') {
                        width: 100%;
                        &:not(:last-child) {
                            margin-bottom: 0.6rem;
                        }
                    }
                }
            }
            .between {
                position: relative;
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-weight: 400;
                font-size: 2.2rem;
                color: #000000;
                @include media('sp') {
                    font-size: 1.6rem;
                }
                & > li {
                    position: relative;
                    flex: 0 0 auto;
                }
                .m {
                    width: calc(50% - 3.6rem);
                    @include media('sp') {
                        width: calc(50% - 1.5rem);
                    }
                }
                select {
                    position: relative;
                    display: block;
                    width: 100%;
                    border-color: #3B8E65;
                    z-index: 0;
                }
            }
            .form-action {
                position: relative;
                margin-top: 4.5rem;
                @include media('sp') {
                    margin-top: 3.0rem;
                }
                .link-btn {
                    position: relative;
                    width: 23.4rem;
                    padding: 1.3rem 4.0rem 1.3rem 4.0rem;
                    margin-left: auto;
                    margin-right: auto;
                    @include media('sp') {
                        width: 20.0rem;
                        padding: 1.3rem 3.2rem 1.3rem 3.2rem;
                        margin-bottom: 0.6rem;
                    }
                    &::after {
                        border-radius: 3.6rem;
                        @include media('sp') {
                            border-radius: 2.7rem;
                        }
                    }
                }
            }
        }
    }
}

// pages content
.merit-block-list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, minmax(0rem, 1fr));
    grid-column-gap: 6.0rem;
    grid-row-gap: 4.0rem;
    @include media('sp') {
        grid-template-columns: 100%;
        grid-column-gap: 2.0rem;
        grid-row-gap: 4.0rem;
    }
    & > li {
        position: relative;
    }
    .merit-block-item {
        position: relative;
        display: block;
