.plan-btn {
    position: relative;
    display: block;
    width: 48.6rem;
    max-width: 100%;
    padding: 0;
    @include media('sp') {
        width: 34.5rem;
    }
    .btn-body {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 7.0rem;
        background: #3B8E65;
        font-weight: 700;
        font-size: 2.0rem;
        line-height: 1.0;
        text-align: center;
        letter-spacing: 0.1em;
        color: #FFFFFF;
        text-decoration: none;
        outline: none;
        border-radius: 3.5rem;
        border: 0.3rem solid #3B8E65;
        transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        overflow: hidden;
        z-index: 1;
        @include media('sp') {
            font-size: 1.6rem;
            line-height: 2.4rem;
            height: 6.0rem;
            border-radius: 3.0rem;
            border-width: 2px;
        }
        span {
            position: relative;
            color: inherit;
            text-decoration: none;
            z-index: 3;
        }
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            background: #FFFFFF;
            width: 100%;
            height: 100%;
            transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
            transform-origin: right top;
            transform: scale(0, 1);
            z-index: 1;
        }
    }
    .btn-left {
        position: absolute;
        top: -1.6rem;
        left: 4.0rem;
        width: initial;
        z-index: 3;
        @include media('sp') {
            top: -1.2rem;
            left: 0.8rem;
            width: 5.2rem;       
        }
    }
    .btn-right {
        position: absolute;
        top: 1.4rem;
        right: 4.0rem;
        width: initial;
        z-index: 3;
        @include media('sp') {
            top: 1.0rem;
            right: 1.0rem;
            width: 5.25rem;
        }
    }
    &:hover {
        .btn-body {
            color: #3B8E65;
            &::before {
                transform-origin: left top;
                transform: scale(1, 1);
            }
        }
    }
}

.plan-found-count {
