            padding: 2.4rem 1.6rem;
            border-width: 2px;
            border-radius: 1.2rem;
        }
        .thumb {
            position: relative;
            width: 26.6rem;
            float: left;
            max-width: 50%;
            @include media('sp') {
                float: none;
                width: 100%;
                max-width: 100%;
                margin-bottom: 2.0rem;
            }
        }
        .wrap {
            position: relative;
            width: calc(100% - 29.8rem);
            float: right;
            @include media('sp') {
                width: 100%;
                float: none;
            }
            .title {
                position: relative;
                font-weight: 700;
                font-size: 2.8rem;
                color: #4E3B13;
                max-width: 40.0rem;
                margin-bottom: 0.6rem;
                @include media('sp') {
                    font-size: 2.0rem;
                    max-width: 100%;
                    margin-bottom: 0.8rem;
                }
                a {
                    color: inherit;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
            .sub {
                position: relative;
                font-weight: 400;
                font-size: 1.6rem;
                line-height: 2.8rem;
                letter-spacing: 0.1em;
                color: #272727;
                margin-bottom: 0.6rem;
                @include media('sp') {
                    font-size: 1.4rem;
                    line-height: 2.4rem;
                }
            }
            .desc {
                position: relative;
                font-weight: 500;
                font-size: 1.8rem;
                color: #4E3B13;
                @include media('sp') {
                    font-size: 1.6rem;
                    line-height: 2.8rem;
                }
            }
            .link-btn {
                position: relative;
                padding: 1.6rem 3.0rem;
                letter-spacing: 0.05em;
                @include media('pc') {
                    position: absolute;
                    top: 0;
                    right: 0;
                }
                @include media('sp') {
                    margin-top: 2.0rem;
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
        .data {
            position: relative;
            margin-top: 2.4rem;
            @include media('sp') {
                margin-top: 3.0rem;
            }
            .list {
                position: relative;
                display: block;
                font-size: 1.6rem;
                line-height: 3.0rem;
                @include media('sp') {
                    font-size: 1.4rem;
                    line-height: 2.4rem;
                }
                & > li {
                    position: relative;
                    display: grid;
                    grid-template-columns: minmax(0rem, 1fr) 8.2rem 24.8rem;
                    grid-column-gap: 0.6rem;
                    @include media('sp') {
                        grid-template-columns: 8.2rem minmax(0rem, 1fr);
                        grid-column-gap: 0.6rem;
                        grid-row-gap: 0.4rem;
                    }
                    &:not(:last-child) {
                        margin-bottom: 1.0rem;
                        @include media('sp') {
                            padding-bottom: 1.0rem;
                            border-bottom: 1px solid #BBBBBB;
                            margin-bottom: 1.0rem;
                        }
                    }
                    strong {
                        position: relative;
                        display: block;
                        font-weight: 500;
                        font-size: 1.8rem;
                        color: #272727;
                        text-align: left;
                        @include media('sp') {
                            font-size: 1.6rem;
                            grid-column: 1 / span 2;
                        }
                    }
                    small {
                        position: relative;
                        display: block;
                        font-weight: 500;
                        font-size: 1.4rem;
                        color: #272727;
                        text-align: left;
                    }
                    span {
                        position: relative;
                        display: block;
                        font-weight: 700;
                        letter-spacing: 0.1em;
                        color: #3B8E65;
                        text-align: right;
                        b {
                            font-size: 1.9rem;
                            @include media('sp') {
                                font-size: 1.8rem;
                            }
                        }
                    }
                }
            }
        }
    }
}

// wp-pagination
.wp-pagination {
