            margin-top: 2.4rem;
            margin-bottom: 2.4rem;
            border-radius: 1.2rem;
        }
        .lead-title {
            position: relative;
            font-size: 1.8rem;
            line-height: 1.5;
            font-weight: 500;
            color: #000000;
            margin-top: 0;
            margin-bottom: 2.0rem;
            @include media('sp') {
                font-size: 1.6rem;
                margin-bottom: 1.6rem;
            }
        }
        .wrap-inline {
            position: relative;
            display: grid;
            grid-template-columns: 21.0rem minmax(0px, 1fr);
            grid-column-gap: 2.0rem;
            margin: 0;
            @include media('sp') {
                grid-template-columns: 12.0rem minmax(0px, 1fr);
                grid-column-gap: 1.2rem;
            }
            &>.left {
                position: relative;
                margin: 0;
            }
            &>.right {
                position: relative;
                margin: 0;
            }
            .thumb {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                text-decoration: none;
                outline: none;
                width: 100%;
                overflow: hidden;
                padding: 0;
                margin: 0;
                img {
                    width: 100%;
                    height: 100%;
                    margin: 0;
                    transition: all 0.3s ease-in-out;
                }
                &:hover {
                    img {
                        transform: scale(1.15);
                    }
                }
            }
            .title {
                position: relative;
                font-family: $font_noto;
                font-size: 1.6rem;
                line-height: 1.8;
                letter-spacing: 0.04em;
                font-weight: 600;
                color: #000000;
                margin: 0;
                @include media('sp') {
                    font-size: 1.4rem;
                    max-width: 100%;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    white-space: normal;
                }
                a {
                    font-family: inherit;
                    font-size: inherit;
                    color: inherit;
                    text-decoration: none;
                    @include media('sp') {
                        text-decoration: underline;
                    }
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
            .meta {
                position: relative;
                margin: 0;
                margin-top: 1.6rem;
            }
            .cat {
                position: relative;
                display: inline-block;
                padding: 0.5rem 1.6rem;
                background-color: $yellow_color;
                font-size: 1.4rem;
                font-weight: 600;
                line-height: 1.5;
                color: #000000;
                text-decoration: none;
                outline: none;
                border-radius: 1.6rem;
                margin: 0;
                @include media('sp') {
                    font-size: 1.2rem;
                    padding: 0.4rem 1.2rem;
                }
                &+.cat {
                    margin-left: 1.2rem;
                    @include media('sp') {
                        margin-left: 1.0rem;
                    }
                }
            }
            a.cat {
                cursor: pointer;
                &:hover {
                    opacity: 0.7;
                }
            }
            .date {
                position: relative;
                display: inline-block;
                font-size: 1.4rem;
                font-weight: 500;
                line-height: 1.5;
                color: #999999;
                margin: 0;
                margin-left: 1.2rem;
                @include media('sp') {
                    margin-left: 1.0rem;
                }
            }
        }
    }
}